import { Component, OnInit } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'app-project-brief',
  templateUrl: './project-brief.component.html',
  styleUrls: ['./project-brief.component.styl']
})
export class ProjectBriefComponent implements OnInit {
  sceneId:any;
  constructor(
    public textEngineService: TextEngineService,
    public router: Router,
    public calcService: CalcService,
    public ar : ActivatedRoute
  ) { }

  ngOnInit() {
    this.ar.params.subscribe((obj:any)=>{
      if(obj) {
        this.sceneId = obj.id;
      }
    });
  }
  nextButton() {
    let nextNode = this.textEngineService.getText('next_Pin_ID', this.sceneId);
    if (nextNode) {
      this.router.navigate(['event', nextNode]);
    } 
  }
}
