  public nextButton() {
    //hide ballon
    this.communicator.trigger(HIDE_BALLOON);


    let rangeName = this.currentScene.split("_");
    if(this.calcService.getValue('tlInput' + rangeName[0] + 'Completed_' + rangeName[1] + '') != '1') {
      this.calcService.setValue('tlInput' + rangeName[0] + 'Completed_' + rangeName[1] + '', 1).then(() => {
        if(this.currentFeedback['next_Pin_ID']) {
          this.textEngineService.nextRoute(this.currentFeedback['next_Pin_ID']);
        }else {
          this.calcService.setValue('tlInputEORFbCompleted_R'+this.currentRound, 1).then((response:any)=>{
          this.router.navigate(['summary']);
          })
        }
      });
    }else {
      if(this.currentFeedback['next_Pin_ID']) {
        this.textEngineService.nextRoute(this.currentFeedback['next_Pin_ID']);
      }else {
        if(this.calcService.getValue('tlInputEORFbCompleted_R'+this.currentRound) != '1'){
          this.calcService.setValue('tlInputEORFbCompleted_R'+this.currentRound, 1).then((response:any)=>{
            this.router.navigate(['summary']);
          })
        }else{
          this.router.navigate(['summary']);
        }
      }
    }
  }

  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }

  markAsComplete(){
    let rangeName = this.currentScene.split("_");
    if(this.calcService.getValue('tlInput' + rangeName[0] + 'Completed_' + rangeName[1] + '') != '1') {
      this.calcService.setValue('tlInput' + rangeName[0] + 'Completed_' + rangeName[1] + '', 1).then(() => {
        if(this.currentFeedback['next_Pin_ID'] == undefined) {
          this.calcService.setValue('tlInputEORFbCompleted_R'+this.currentRound, 1).then((response:any)=>{
            // console.log("Round marked as completed")
          })
        }
      });
    }
  }

  getFakeTableData(){
    this.categorylabels = this.currentFeedback["chartcategories"] ? this.currentFeedback["chartcategories"].split(',') : [];
    this.currentFeedback["chartranges"] = this.currentFeedback["chartranges"].replace(/&quot;/g, '"');
    this.rangeref = this.currentFeedback["chartranges"] ? JSON.parse(this.currentFeedback["chartranges"]) : [];
    
    let res = ChartData[this.currentScene]
    if(res.success) {
      this.participantsStats = JSON.parse(res.questions);
      this.createDataForTable();
      this.showChart = true;
    }
  }

