import { Component, OnInit, Input } from '@angular/core';
import { CalcService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'app-progress',
  templateUrl: './progress.component.html',
  styleUrls: ['./progress.component.styl']
})
export class ProgressComponent implements OnInit {
  progressCount: any;
  modelChange:any;
  constructor(
    public calcService: CalcService,
    public communicator : CommunicatorService
  ) { }

  ngOnInit() {
    this.getProgress();
    this.modelChange = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe((val)=>{
      this.getProgress();
    })
  }
  getProgress() {
    this.progressCount = this.calcService.getValueForYear('tlOutputProgress', 'tlInputRound');
  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    if(this.modelChange) {
      this.modelChange.unsubscribe();
    }
  }

}
