import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpWrapperService } from '@btsdigital/ngx-isomer-core';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'ism-logout',
  template: '<ng-content></ng-content>'
})

export class LogoutComponent implements OnInit {

  constructor(private http: HttpClient, private httpWrapper: HttpWrapperService) { }

  postUrl(relativeUrl: string): Promise<any> {
      const hostname = environment.hostname;
      const url = window.location.protocol + '//' + hostname + relativeUrl;
      return this.http.post(url, null, { withCredentials: true }).toPromise();
  }

  ngOnInit() {
      // pulse logout
      const logoutUrl = (environment.CDN0LOCAL1LOCALHOST2 == 0) ? '/Wizer/CloudFront/Logout' : '/Wizer/Wizer/Logout';
      this.httpWrapper.postJson(logoutUrl, null)
          .then(result => {
              // console.log('logged out from pulse ' + JSON.stringify(result))
              // clear all cookies
              // console.log('cookies before:'+ document.cookie)
              const cookies = document.cookie.split(';');
              for (let i = 0; i < cookies.length; i++) {
                  const cookie = cookies[i].split('=')[0].trim();
                  if (cookie.length > 0) {
                      // console.log(cookie)
                      document.cookie = cookie + '=; expires=Thu, 01 Jan 1970 00:00:00 UTC';
                  }
              }

              setTimeout(() => {
                  // console.log('cookies after:'+ document.cookie)
                  if (environment.CDN0LOCAL1LOCALHOST2 == 1) {
                      window.location.href = window.location.protocol + '//' + window.location.hostname + '/sim/login/';
                  } else {
                      window.location.href = window.location.protocol + '//' + window.location.hostname +
                          ((window.location.port) ? ':' + window.location.port : '');
                  }
              }, 200);

          })
  }
}
