import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';

@Component({
  selector: 'app-singleselect',
  templateUrl: './singleselect.component.html',
  styleUrls: ['./singleselect.component.styl']
})
export class SingleselectComponent implements OnInit {
  @Input() data: any;
  @Input() currentEventData: any;
  @Input() routeTo: any;
  @Input() id : any;

  @Output() continue: EventEmitter<any> = new EventEmitter<any>();

  responseData = [];
  selectedData: any;
  buttonState = true;
  value: any;
  currentRound: string;
  currentMoment : string;
  progressCount: string;
  selectionDone = false;
  private contentUpdateSubscription: Subscription;

  constructor(
    private router: Router,
    public textEngineService: TextEngineService,
    private calcService: CalcService,
    public cdref : ChangeDetectorRef,
  ) { }

  ngOnInit() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.initializeAll();
    });
  }
  initializeAll() {
    this.responseData = this.data;
    // console.log(this.responseData)
    for (let i = 0; i < this.responseData.length; i++) {
      if(this.responseData[i].narrative) {
        this.responseData[i].narrative = this.responseData[i].narrative.replace(/&quot;/g, '"');
      }
    }
    this.currentMoment = ((this.router.url.split('/').pop()).split('_')[0]).split('Moment')[1];
    if(!this.currentMoment) {
      this.currentMoment ='1';
    }
    this.checkIfVisited();
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.getProgress();
  }
  checkIfVisited() {
    setTimeout(() => {
      let round = this.currentEventData.ID.split('_').pop();
      let moment = this.currentEventData.ID.split('_')[0];
      if(this.calcService.getValue('tlInput'+moment+'DecCompleted_'+round) == '1') {
        let value = this.calcService.getValue(this.responseData[0]['baseRangeName']);
        let index = Number(value)-1;
        this.onItemChange(null,index);
        // let a:any = document.getElementById('radioCircle'+index);
        // a.click();
        this.selectionDone = true;
      }
    },);
  }
  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }

  public onItemChange(ev,index) {
    if(ev){
      ev.preventDefault();
    }
    this.selectedData = this.responseData[index];
    this.responseData.map((item, i)=>{
      item.value = false;
      if( i == index) {
        item.value = true;
      }
    })
    this.value = index + 1;
    // console.log(this.responseData);
    this.cdref.detectChanges()

    this.buttonState = false;
  }
  ngOnDestroy() {
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }

  public submitResponse() {
    if(!this.selectionDone) {
      this.calcService.setValue(this.responseData[0]['baseRangeName'], this.value).then(async () => {
        this.continue.emit(1);
        // this.router.navigate([this.routeTo, this.currentEventData.next_Pin_ID]);
        this.textEngineService.nextRoute(this.currentEventData['next_Pin_ID'], false);

      })
    }else {
      // this.router.navigate([this.routeTo, this.currentEventData.next_Pin_ID]);
      this.textEngineService.nextRoute(this.currentEventData['next_Pin_ID'], true);
    }
  }
  ngOnChanges(changes: SimpleChanges): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
    
    
  }
}
