import { Component, OnInit, HostListener } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router } from '@angular/router';
import { CalcService, StorageService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-mission',
  templateUrl: './mission.component.html',
  styleUrls: ['./mission.component.styl']
})
export class MissionComponent implements OnInit {
  currentRound: string;
  missionContent: string;
  progressCount: string;
  private contentUpdateSubscription: Subscription;
  constructor(
    private textEngineService: TextEngineService,
    private router: Router,
    protected calcService: CalcService,
    private storageService: StorageService
  ) { }


  ngOnInit() {
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.currentRound = this.calcService.getValue('tlInputRound');
      this.missionContent = this.textEngineService.getText('missioncontent');
      this.getProgress();
    });
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.missionContent = this.textEngineService.getText('missioncontent');
    this.getProgress();
  }
  ngOnDestroy(): void {
    if (this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe()
    }
  }
  nextButton() {
    this.calcService.setValue('tlInputIntro3Completed_R' + this.currentRound, 1);
    this.router.navigate(['yourcoach']);
  }

  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }

}
