import { Component, HostListener, OnInit } from '@angular/core';
import { StorageService, CommunicatorService, CalcService, TextService, SyncService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import { BootstrapService } from './shared/bootstrap.service';
import { APP_READY, CHANGE_LANGUAGE, LANGUAGE_LOADED, REFS } from './utils/constants';
import { Subscription } from 'rxjs';
import { TextEngineService } from './shared/textengine/textengine.service';
@Component({
  selector: 'ism-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.styl']
})
export class AppComponent implements OnInit {

  isAppReady = false;
  private languageChangeSubscription: Subscription;
  selecteLanguage;
  isIE: boolean;
  constructor(
    private storageService: StorageService,
    private router: Router,
    private bootstrapper: BootstrapService,
    private communicator: CommunicatorService,
    private textService: TextService,
    private textEngineService: TextEngineService,
    public syncService: SyncService,

    private calcService: CalcService
  ) { }

  ngOnInit() {
    // show dashboard only after APP_READY is set from bootstrap
    this.checkRound();
    this.isIE = this.detectIE();
    this.bootstrapper.initAppConfig();
    this.communicator.getEmitter(APP_READY).subscribe((value) => {
      // if((value + '') === 'true'){
      //   let round = localStorage.getItem("roundNo")
      //   if(round != null){
      //     this.calcService.setValue(REFS.CURRENT_ROUND,round).then((obj:any)=>{
      //     }).catch((err)=>{
      //       console.log(err);
      //     })
      //   }
      // }
      this.isAppReady = ((value + '') === 'true') ? true : false;
      if (this.isAppReady) {
        this.selecteLanguage = this.calcService.getValue('tlInputLanguage');
        if (this.selecteLanguage && this.selecteLanguage != 'en') { //for avoiding to load same language(en) again.. 
          this.textEngineService.loadLangContent(this.selecteLanguage);
        }
      }

    });
    this.languageChangeSubscription = this.communicator.getEmitter(CHANGE_LANGUAGE).subscribe((value) => {
      this.calcService.setValue('tlInputLanguage', value).then((res) => {
        this.syncService.Upload();
        this.calcService.saveStateToStorage().then(() => {
          this.selecteLanguage = value;
          this.textEngineService.loadLangContent(value);
        });
      });
    })
  }

  checkRound() {
    var QueryParams = this.getQueryParams();
    if (QueryParams.length > 0) {
      this.setRoundDetails(QueryParams)
    }

  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    this.languageChangeSubscription.unsubscribe();
  }


  onActivate(event) {
    window.scroll(0, 0);
    //or document.body.scrollTop = 0;
    //or document.querySelector('body').scrollTo(0,0)
  }

  getQueryParams() {
    var paramString = location.href.split('?')[1];
    var params = [], paramArray = [];
    if (paramString) {
      params = paramString.split('&');
      if (params && params.length > 0) {
        for (let i = 0; i < params.length; i++) {
          var paramObject = {};
          var splitted = params[i].split('=');
          paramObject[splitted[0]] = splitted[1];
          paramArray.push(paramObject);
        }
      }
    }
    return paramArray;
  };

  setRoundDetails(params) {
    // localStorage.setItem("roundNo", null);
    for (var i = 0; i < params.length; i++) {
      if (params[i]["roundNo"] != undefined) {
        try {
          localStorage.setItem("roundNo", params[i]["roundNo"]);
        } catch (error) {
          console.log("INDSIDE APP COMPONENT. ERROR SETTING ROUND TO LOCAL STORAGE")
        }
      }
    }
  }

  detectIE() {
    let ua = window.navigator.userAgent;

    let msie = ua.indexOf('MSIE ');
    if (msie > 0) {
      // IE 10 or older => return version number
      return true;
    }

    let trident = ua.indexOf('Trident/');
    if (trident > 0) {
      // IE 11 => return version number
      let rv = ua.indexOf('rv:');
      return true;
    }

    let edge = ua.indexOf('Edge/');
    if (edge > 0) {
      // Edge (IE 12+) => return version number
      return true;
    }

    // other browser
    return false;
  }
}
