import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { CalcService, CommunicatorService, HttpWrapperService, SyncService } from '@btsdigital/ngx-isomer-core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router } from '@angular/router';
import { environment } from '../../../environments/environment';
import { XApiService } from '@btsdigital/pulseutilities';
import { PasswordComponent } from 'src/app/shared/password.component/password.component';
import { Subscription } from 'rxjs';
import { leaderboardData } from 'src/app/utils/leaderboard-data';
const KEY_NUM_1 = 49;
const MAX_ROUNDS = 4;
@Component({
  selector: 'app-summary',
  templateUrl: './summary.component.html',
  styleUrls: ['./summary.component.styl']
})
export class SummaryComponent implements OnInit {
  environment = environment;
  @ViewChild('passwordModal', { static: true }) public passwordModalRef: PasswordComponent;
  currentRound: any;
  summaryKeys = [];
  summary = null;
  summaryData = null;
  progressCount: string;
  leaderboardQuestionArray = [];
  score: string;
  participants = [];
  myRank:any;
  totalParticipants = null;
  partInfo: any;
  showLeaderboard = false;
  filterValue = 20;
  displayShowMore = true;
  showMoreLoading = false;
  showTryAgain = false;
  hideButton = false;
  private contentUpdateSubscription: Subscription;
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_1) {
      this.showNextButton();
    }
  }
  constructor(
    public textEngineService: TextEngineService,
    public calcService: CalcService,
    public router : Router,
    public httpWrapperService: HttpWrapperService,
    public syncService: SyncService,
    private xpiService: XApiService
  ) { }

  showMore() {
    this.showMoreLoading = true
    this.filterValue = this.filterValue + 20;
    this.getLeaderboardData();
    this.decideShowMoreDisplay();
  }

  decideShowMoreDisplay() {
    if(this.filterValue >= this.totalParticipants) {
      this.displayShowMore = false;
    }else {
      this.displayShowMore = true;
    }
  }

  showNextButton() {
    this.hideButton = !this.hideButton;
  }
  
  ngOnInit() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.getSummaryObj();
    });
  }
  initializeAll () {
    this.getProgress();
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.leaderboardQuestionArray = ['tlOutputMetric1_R'+this.currentRound, 'tlInputName_R1'];
    this.getScore();
    if(this.environment.connectToPulse) {
      this.partInfo = this.syncService.getParticipantInfo();
      if (this.partInfo.participantId == undefined) {
        this.keepGettingPartInfo();
      }else {
        this.getLeaderboardData();
      }
    } else {
      this.getFakeLeaderboardData();
    }
    this.getSummaryObj();
    // this.calcService.setValue('tlInputEORFbCompleted_R'+this.currentRound, 1);
    // console.log(this.calcService.getValueForYear('tlOutputProgress', 'tlInputRound'))
    if(this.calcService.getValueForYear('tlOutputProgress', 'tlInputRound') == '6') {
      this.calcService.setValue('tlInputSubmitted_R'+this.currentRound, 1).then(()=>{
        this.syncService.Upload();
        this.calcService.saveStateToStorage().then(()=>{
          this.sendXapiCalls();
          
          if(this.currentRound == MAX_ROUNDS && environment.isLms){
            this.calcService.setValue('LMS_FINISHED', 'true');
          }
        })
      });
    }
  }
  ngOnDestroy() {
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }
  getScore() {
    this.score = this.calcService.getValueForYear('tlOutputMetric1', 'tlInputRound');
  }
  getLeaderboardData() {
    let body = {
      questions: this.leaderboardQuestionArray,
      trackQuestionName: null,
      sortOrder: 'desc',
      sortByQuestion: 'tlOutputMetric1_R'+this.currentRound,
      range: 0,
      rankQuestionName: null,
      listSkip: this.filterValue - 20,
      listLength: 20,
      voteIsStringFormat: true,
      showRank: true
    };
    this.httpWrapperService.postJson('/Wizer/CloudFront/GetLeaderboardResult', body).then((res:any)=>{
      if(res.success) {
        let people = res['votes'];
        this.participants = this.participants.concat(people);
        this.totalParticipants = res['totalParticipants']; // total unique users
        this.myRank = res['myRank'];
        this.showLeaderboard = true;
        this.decideShowMoreDisplay();
        this.showMoreLoading = false;
        this.showTryAgain = false;
      }
    }).catch((err)=>{
      this.showTryAgain = true;
    })
  }

  getSummaryObj() {
    if (this.currentRound) {
      this.summaryData = this.textEngineService.getSceneById('Summary_R' + this.currentRound)['Summary_R' + this.currentRound];
      if (this.summaryData) {
        this.summary = {}
        for (const key in this.summaryData) {
          if (key.includes('summ_head')) {
            let index = Number((key.split('_')[1]).split('head').pop());
            if ((this.summary.hasOwnProperty('headings'))) {
              if (this.summary['headings'][index - 1]) {
                this.summary['headings'][index - 1][key] = this.summaryData[key]
              } else {
                this.summary['headings'][index - 1] = {};
                this.summary['headings'][index - 1][key] = this.summaryData[key]
              }
            } else {
              this.summary['headings'] = []
              this.summary['headings'][index - 1] = {}
              this.summary['headings'][index - 1][key] = this.summaryData[key]
            }
          } else {
            this.summary[key] = this.summaryData[key]
          }

        }
      }
    }
  }

  sendXapiCalls() {
    if(this.calcService.getValue('tlInputSubmitted_R'+this.currentRound) == '1') {
      const year = this.currentRound;
      try {
        if (String(year) == '1') {
          this.xpiService.trackEvent('http://adlnet.gov/expapi/verbs/completed', null, null, true, null, null, null, null, null, null, null, false, false, null, null, false, 'tped_prod_1');
        } else if (String(year) == '2') {
          this.xpiService.trackEvent('http://adlnet.gov/expapi/verbs/completed', null, null, true, null, null, null, null, null, null, null, false, false, null, null, false, 'tped_prod_2');
        } else if (String(year) == '3') {
          this.xpiService.trackEvent('http://adlnet.gov/expapi/verbs/completed', null, null, true, null, null, null, null, null, null, null, false, false, null, null, false, 'tped_prod_3');
        } else if (String(year) == '4') {
          this.xpiService.trackEvent('http://adlnet.gov/expapi/verbs/completed', null, null, true, null, null, null, null, null, null, null, false, false, null, null, false, 'tped_prod_4');
        }
      } catch (error) {
        console.log(error);
      }
    }
  }

  keepGettingPartInfo() {
    this.partInfo = this.syncService.getParticipantInfo();
    if (this.partInfo.participantId == undefined) {
      setTimeout(() => {
        this.keepGettingPartInfo()
      }, 1000);
      return;
    }
    this.getLeaderboardData();
  }

  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }

  checkLMS() {
    if(this.environment.isLms) {
      this.passwordModalRef.showModal();
    }else {
      this.nextButton()
    }
  }

  nextButton() {
    if(Number(this.currentRound)+1 <=4 ) {
      this.calcService.setValue('tlInputRound', Number(this.currentRound)+1);
      localStorage.setItem("roundNo", (Number(this.currentRound)+1)+'')
      this.router.navigate(['welcome'])
    }else {
      alert('All rounds are completed')
    }
  }

  tryFetchingDataAgain(){
    this.showTryAgain = false;
    this.getLeaderboardData();
  }

  getFakeLeaderboardData() {
    let res = leaderboardData["R"+this.currentRound]
    this.participants = []
    if(res.success) {
      let people = res['votes'];
      this.participants = this.participants.concat(people);
      this.participants.map((item)=>{
        item.tlInputName_R1 = this.shuffelWord(item.tlInputName_R1)
      })
      this.totalParticipants = res['totalParticipants']; // total unique users
      this.myRank = res['myRank'];
      this.showLeaderboard = true;
      this.displayShowMore = false; //this.decideShowMoreDisplay();
      this.showMoreLoading = false;
      this.showTryAgain = false;
    }
  }

  shuffelWord(word) {
    var shuffledWord = '';
    word = word.split('');
    while (word.length > 0) {
      shuffledWord += word.splice(word.length * Math.random() << 0, 1);
    }
    return shuffledWord;
  }
}

