import { Component, Input, OnInit, AfterViewInit, EventEmitter, ViewChild, OnDestroy, HostListener, Output } from '@angular/core';
import { EVENTS, SCENARIO, ROUTES, GENERAL } from '../../utils/constants';
import { CalcService, StorageService, CommunicatorService, IsomerXapiService, Constants } from '@btsdigital/ngx-isomer-core';
import { TextEngineService } from '../../shared/textengine/textengine.service';
import { Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';
import { WobblerTimingService } from '../../wobbler-timing.service';
import { ThrowStmt } from '@angular/compiler';


@Component({
  selector: 'ism-wobbler',
  templateUrl: './wobbler.component.html',
  styleUrls: ['./wobbler.component.styl']
})
export class WobblerComponent implements OnInit {


  @Output() optionSelected = new EventEmitter<boolean>();
  private selfRef;
  private wobblersArray = [];
  private wobbler: any;
  private wobblerLoc = -1;
  private name = "";
  private narrative = "";
  private reviewBtntext = "";
  private closeReviewBtnBtn = "";
  private background = "";
  private choices = [];
  private isViewed = false;
  private answer = -1;
  private reviewAnswer = -1;
  private selectedValue = -1;
  private feedback = "";
  private ansName = "";
  private currentYear = 1;
  private currentPhase = 1;
  private readOnly: boolean = false;
  private isInfo: boolean = false;
  private animate: boolean = false;
  private wobblerTImingSubscription: Subscription;
  public wobblerData = {};
  activeBtn = false;
  reviewedData: any = {};
  newReviewData: any = {};
  showWobblers = false;
  reviewBtnSwitch = true;


  @Output() WobblerTriggered: EventEmitter<any> = new EventEmitter<any>();


  constructor(private commmunicator: CommunicatorService, private router: Router, private calcService: CalcService, private textEngineService: TextEngineService, private dataAdaptor: StorageService, private wobblerTiming: WobblerTimingService, private isomerXapiService: IsomerXapiService) { }

  ngOnInit() {
    let self = this;
    self.initializeWobblers();
    this.wobblerTImingSubscription = this.wobblerTiming.getObservable().subscribe(() => {
      this.WobblerTriggered.emit();
      this.showWobbler(self);
    });
  }


  selectOption(index) {
    this.activeBtn = true;

    if (!this.isViewed) {
      this.answer = index + 1;
    }

  }
  showWobbler(self) {
    let conditional = false;
    let index = 1;

    if (self == undefined) {
      self = this.selfRef;
    }

    self.answer = -1;

    self.name = self.wobbler.name;
    self.reviewBtntext = self.wobbler.reviewBtntext;
    self.closeReviewBtnBtn = self.wobbler.closeReviewBtnBtn;
    self.background = self.wobbler.background;
    self.narrative = self.wobbler.narrative;
    self.isViewed = self.calcService.getValue(self.wobbler["wobblerRange"]) == 0 ? false : true;

    if (self.isViewed) {
      let alphabet = ["a", "b", "c", "d", "e", "f", "g", "h", "i", "j", "k", "l", "m", "n", "o", "p", "q", "r", "s", "t", "u", "v", "w", "x", "y", "z"];
      let ans: string = self.calcService.getValue(self.wobbler["wobblerRange"]).toString();

      let letterPosition = ans.slice(ans.length - 1);

      if (letterPosition != "") {
        self.wobbler["feedback"] = self.wobbler["alt" + letterPosition + "_feedback"];
        self.feedback = self.wobbler.feedback;
        self.wobbler["reviewAnswer"] = letterPosition;
        self.reviewAnswer = self.wobbler.reviewAnswer;
        self.wobbler["selectedValue"] = self.reviewAnswer;
        self.selectedValue = self.reviewAnswer;
        self.wobbler["ansName"] = self.wobbler["alt" + self.reviewAnswer + "_name"];
        self.ansName = self.wobbler["ansName"];
      }

      self.animate = true;
    } else {
      self.animate = false;
    }

    if (self.narrative == "conditional") {
      self.narrative = "";

      let result = 0;
      let cond = self.wobbler["narrative" + index + "_conditional"];
      while ((cond != undefined) && (index < 100)) {
        result = self.calcService.getValue(cond);
        if (result == 1) {
          conditional = true;
          self.narrative = self.wobbler["narrative" + index];
          break;
        }
        index++;
        cond = self.wobbler["narrative" + index + "_conditional"];
      }
      self.isViewed = true;
      self.readOnly = true;
    }
    if (self.wobbler["type"] == "InfoUpdate") {
      self.isViewed = true;
      self.readOnly = true;
    }

    self.choices = [];

    let i = 1;
    while (self.wobbler["alt" + i] != undefined) {
      self.choices.push({
        "name": self.wobbler["alt" + i + "_name"],
        "value": self.wobbler["alt" + i],
        "label": self.wobbler["alt" + i + "_narrative"],
        "classLabel": (self.wobbler["alt" + i].toLowerCase() + "-button"),
        "isViewed": (self.isViewed)
      })
      i++;
    }
    this.wobblerData = self;
    this.showWobblers = true;
    this.commmunicator.trigger(GENERAL.SHOW_WOBBLER);

  }
  reviewSelectOption(index) {

    this.selectedValue = index + 1;
    this.newReviewData = this.wobblerData['choices'][index];
    this.newReviewData['feedback'] = this.wobbler["alt" + this.selectedValue + "_feedback"];
  }

  submit() {

    if (this.answer != -1 || this.readOnly) {
      this.feedback = this.wobbler["alt" + this.answer + "_feedback"];
      this.wobbler["feedback"] = this.wobbler["alt" + this.answer + "_feedback"];
      this.ansName = this.wobbler["alt" + this.answer + "_name"];
      this.wobbler["ansName"] = this.wobbler["alt" + this.answer + "_name"];
      this.reviewAnswer = this.answer;
      this.wobbler["reviewAnswer"] = this.answer;
      this.selectedValue = this.answer;
      this.wobbler["selectedValue"] = this.answer;
      this.wobbler["wobblerCode"] = this.wobbler["type"] == "InfoUpdate" ? this.wobbler["wobblerCode"] : this.wobbler["wobblerCode"] + "0" + this.answer;



      //  this.isomerXapiService.resetTimeInLog();
      this.calcService.setValue(this.wobbler["wobblerRange"], this.wobbler["wobblerCode"], false, String(this.currentYear), 'wobblers');

      console.log(this.wobbler["wobblerRange"] + " set to " + this.wobbler["wobblerCode"]);
      this.wobbler["isViewed"] = true;
      this.isViewed = true;
      this.reviewedData = this.wobbler;
      this.reviewSelectOption(this.answer - 1);
      const dom = document.querySelector('.flipper') as any;
      // Code for Safari
      dom.style.WebkitTransform = 'rotateY(-180deg)';
      // Code for IE9
      dom.style.msTransform = 'rotateY(-180deg)';
      // Standard syntax
      dom.style.transform = 'rotateY(-180deg)';
      // For IE 11
      const doc = document as any;
      if ((navigator.userAgent.indexOf('MSIE') !== -1) || (!!doc.documentMode === true)) {
        const front = document.querySelector('.front') as any;
        front.style.WebkitBackfaceVisibility = 'visible';
        front.style.msBackfaceVisibility = 'visible';
        front.style.backfaceVisibility = 'visible';

        front.style.WebkitTransform = 'none';
        front.style.msTransform = 'none';
        front.style.transform = 'none';
        front.style.height = '50px';
        front.style.overflow = 'hidden';

        const back = document.querySelector('.back') as any;
        back.style.WebkitBackfaceVisibility = 'visible';
        back.style.msBackfaceVisibility = 'visible';
        back.style.backfaceVisibility = 'visible';
      }
    }
  }

  backSubmit() {
    const dom = document.querySelector('.flipper') as any;
    // Code for Safari
    dom.style.WebkitTransform = 'rotateY(-0deg)';
    // Code for IE9
    dom.style.msTransform = 'rotateY(-0deg)';
    // Standard syntax
    dom.style.transform = 'rotateY(-0deg)';
    // For IE 11
    const doc = document as any;
    if ((navigator.userAgent.indexOf('MSIE') !== -1) || (!!doc.documentMode === true)) {
      const front = document.querySelector('.front') as any;
      front.style.WebkitTransform = 'rotateY(0deg)';
      front.style.msTransform = 'rotateY(0deg)';
      front.style.transform = 'rotateY(0deg)';
      front.style.height = 'auto';
      front.style.overflow = 'visible';

      const back = document.querySelector('.back') as any;
      back.style.WebkitBackfaceVisibility = 'hidden';
      back.style.msBackfaceVisibility = 'hidden';
      back.style.backfaceVisibility = 'hidden';

    }
  }

  reviewDecision() {
    this.reviewBtnSwitch = !this.reviewBtnSwitch;
  }

  decisionSubmit() {
    const maincontainer = document.querySelector('.wobbler') as any;
    maincontainer.style.display = 'none';
    maincontainer.style.overflow = 'hidden';
    this.optionSelected.emit(true);
    this.commmunicator.trigger(GENERAL.HIDE_WOBBLER);
  }

  initializeWobblers() {
    let wobblers = this.textEngineService.getMeetings();
    let index = 0;
    this.wobblerLoc = -1;
    this.wobblersArray = [];
    this.currentYear = parseInt(this.calcService.getValue("xxRound"));
    //set value of the year
    this.dataAdaptor.setValue(Constants.ROUND_VALUE, String(this.currentYear));
    this.currentPhase = 1;

    console.log("wobblers found ...");
    for (var key in wobblers) {
      var localScene = [];
      for (var property in wobblers[key]) {
        if (wobblers[key].hasOwnProperty(property)) {
          localScene[property] = wobblers[key][property];
        }
      }

      localScene["wobblerNum"] = 0;


      localScene["wobblerNum"] = parseInt(localScene["ID"].substr(localScene["ID"].length - 2));
      localScene["wobblerRange"] = "tlInputWobbler" + localScene["rangesuffix"];
      localScene["wobblerCode"] = parseInt(localScene["rangesuffix"]);
      //track data on load
      this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(localScene["wobblerRange"]), this.calcService.getRefNameByYearRef(localScene["wobblerRange"], ''), String(localScene["wobblerCode"]), String(this.currentYear), 'wobblers', false);
      // localScene["wobblerCode"] = parseInt(localScene["ID"].substr(1,1));
      localScene["isViewed"] = (this.calcService.getValue(localScene["wobblerRange"]) == "0") || (this.calcService.getValue(localScene["wobblerRange"]) == undefined) ? false : true;
      this.isViewed = localScene["isViewed"];

      if (this.isViewed) {
        let alphabet = ["a", "b", "c", "d", "e", "f", "g", "h", "i", "j", "k", "l", "m", "n", "o", "p", "q", "r", "s", "t", "u", "v", "w", "x", "y", "z"];
        let ans: string = this.calcService.getValue(localScene["wobblerRange"]).toString();

        let letterPosition = ans.slice(ans.length - 1);
        if (letterPosition != "") {
          localScene["feedback"] = localScene["alt" + letterPosition + "_feedback"];
          localScene["reviewAnswer"] = letterPosition;
          localScene["selectedValue"] = letterPosition;
          localScene["ansName"] = localScene["alt" + letterPosition + "_name"];
        }
      }

      if (parseInt(localScene["round"]) == this.currentYear && parseInt(localScene["phase"]) == this.currentPhase) {
        console.log(localScene["ID"]);
        this.wobblersArray.push(localScene);

        let completed = (this.calcService.getValue(localScene["wobblerRange"]) != "0");
        localScene["isViewed"] = completed;
        if (!completed && (this.wobblerLoc == -1)) {
          this.wobblerLoc = index;
        }
        index++;
      }
      console.log(localScene["PageType"]);
      if (localScene["PageType"] == "InfoUpdate") {
        this.isInfo = true;
      }

    }

    if (this.wobblerLoc != -1) {

      this.startWobllerTiming();

    }

  }
  startWobllerTiming() {
    this.wobbler = this.wobblersArray[this.wobblerLoc];
    // let self = this;
    this.dataAdaptor.getValue(GENERAL.WOBBLER_TIMING, true).then((res) => {
      let storedTime = res && res != '' ? parseInt(res.toString()) : 0
      let Actulatime = parseInt(this.wobbler["timingRef"]);
      let remainingTime = Actulatime * 60 - storedTime;
      this.wobblerTiming.startTime(remainingTime);
    });
  }





}
