import { Component, OnInit } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-welcomescreen',
  templateUrl: './welcomescreen.component.html',
  styleUrls: ['./welcomescreen.component.styl']
})
export class WelcomescreenComponent implements OnInit {
  welcomeContent: string;
  currentRound: string;
  progressCount: string;
  private contentUpdateSubscription: Subscription;
  constructor(
    public textEngineService: TextEngineService,
    public router: Router,
    public calcService: CalcService,
  ) { }

  ngOnInit() {
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.currentRound = this.calcService.getValue('tlInputRound');
      this.welcomeContent = this.textEngineService.getText('welcomecontent');
    });
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.welcomeContent = this.textEngineService.getText('welcomecontent');
  }
  ngOnDestroy(): void {
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe()
    }
  }
  nextButton() {
    let nextNode = this.textEngineService.getText('welcome_next_node', 'Intro_R'+this.currentRound);
    if(nextNode) {
      if(this.calcService.getValue('tlInputIntro1Completed_R'+this.currentRound ) != '1') {
        this.calcService.setValue('tlInputIntro1Completed_R'+this.currentRound, 1);
      }
      this.router.navigate([nextNode]);
    }else {
      if(this.calcService.getValue('tlInputIntro2Completed_R' + this.currentRound) != '1'){
        this.calcService.setValue('tlInputIntro2Completed_R' + this.currentRound, 1);
      }
      this.router.navigate(['mission']);
    }
  }

}
