import { Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { CommunicatorService, TextService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';

@Component({
  selector: 'isma-text-output',
  templateUrl: './text-output.component.html',
  styleUrls: ['./text-output.component.styl']
})
export class TextOutputComponent implements OnInit {
  /**
    * Input binding for text key
    *
    */
  @Input() key: string;
  /**
   * Input binding for yearRef named range
   *
   */
  @Input() yearRef: string;
  /**
   * Input binding for flag to display text as innerHtml instead of a text node.
   *
   */
  @Input() asInnerHtml = false;
  /**
   * Input binding for sceneID reference from content json
   *
   */
  @Input() sceneId: string;
  /**
   * Text content for the text key in content json.
   *
   */
  value: string;
  /**
   * Subscription for Language loaded emitter.
   *
   */
  private contentUpdateSubscription: Subscription;
  constructor(private communicator: CommunicatorService, public textEngineService: TextEngineService) { }

  ngOnInit() {
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.updateText();
    });
    this.updateText();
  }

  updateText() {
    this.value = this.textEngineService.getTextForYear(this.key, this.yearRef, this.sceneId) ? this.textEngineService.getTextForYear(this.key, this.yearRef, this.sceneId) : this.key;
  }

  ngOnChanges(changes: SimpleChanges): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
    const change: any = changes['key'];
    if (change && change.isFirstChange()) {
      return;
    }
    this.updateText();
  }

  ngOnDestroy() {
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }

}
