import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';

@Component({
  selector: 'app-multiselect',
  templateUrl: './multiselect.component.html',
  styleUrls: ['./multiselect.component.styl']
})
export class MultiselectComponent implements OnInit {

  @Input() data: any;
  @Input() currentEventData: any;
  @Input() round: any;
  @Input() noOfSelection: any;
  @Input() minNoOfSelections: any;
  @Input() id: any;

  // @Input() saveToRangeName: any;
  @Input() routeTo: any;

  @Output() continue: EventEmitter<any> = new EventEmitter<any>();

  responseData = [];
  selectedData = "B";
  buttonState = true;
  value: any;

  checkboxIncrement = [];
  disableCheckbox: boolean = false;
  progressCount: string;
  currentRound: string;
  selectionDone: boolean = false;
  currentMoment: string;
  private contentUpdateSubscription: Subscription;

  constructor(
    private router: Router,
    public textEngineService: TextEngineService,
    private calcService: CalcService,
    public cdref : ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.initializeAll();
    });
  }
  initializeAll() {
    this.currentMoment = ((this.router.url.split('/').pop()).split('_')[0]).split('Moment')[1];
    if(!this.currentMoment) {
      this.currentMoment ='1';
    }
    this.checkIfVisited();
    this.responseData = this.data;
    if (this.minNoOfSelections > 0) {
      this.buttonState = true;
    } else {
      this.buttonState = false;
    }
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.getProgress();
  }

  checkIfVisited() {
    setTimeout(() => {
      let round = this.currentEventData.ID.split('_').pop();
      let moment = this.currentEventData.ID.split('_')[0];
      let checkboxElement = document.getElementsByName("responseData");
      if (this.calcService.getValue('tlInput' + moment + 'DecCompleted_' + round) == '1') {
        for (let i = 0; i < this.responseData.length; i++) {
          let value = this.calcService.getValue(this.responseData[i]['baseRangeName']);
          if (value == '1') {
            this.responseData[i]['value'] = true;
          } else {
            this.responseData[i]['value'] = false;
          }
        }
        this.buttonState = false;
        this.selectionDone = true;

      }
      this.cdref.detectChanges()
    });
  }
  ngOnDestroy() {
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }
  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }

  public onCheckBoxChange(ev) {
    ev.stopPropagation()
    this.disableEnableAllCheckBoxes();
    for (let i = 0; i < this.responseData.length; i++) {
      if (this.responseData[i].value) {
        this.selectedData = "A";
        return;
      } else {
        this.selectedData = "B";
      }
    }
  }

  disableEnableAllCheckBoxes() {
    this.checkboxIncrement = [];
    for (let i = 0; i < this.responseData.length; i++) {
      if (this.responseData[i].value) {
        this.checkboxIncrement.push("1");
      }
    }
    if (this.checkboxIncrement.length >= this.minNoOfSelections) {
      this.buttonState = false;
    } else {
      this.buttonState = true;
    }
    this.disableBoxes();
  }

  disableBoxes() {
    setTimeout(() => {
      let checkboxElement = document.getElementsByName("responseData");
      if (checkboxElement) {
        for (let i = 0; i < checkboxElement.length; i++) {
          if (checkboxElement[i]['value'] === "false" && this.checkboxIncrement.length >= this.noOfSelection) {
            checkboxElement[i]['disabled'] = true;
            checkboxElement[i].nextSibling['classList'].add("disabled-checkbox");
          } else {
            checkboxElement[i]['disabled'] = false;
            checkboxElement[i].nextSibling['classList'].remove("disabled-checkbox");
          }
        }
      }
    }, 100);
  }

  // public minSelections() {
  //   if (this.checkboxIncrement.length >= this.minNoOfSelections) {
  //     this.buttonState = false;
  //   }
  // }

  public submitResponse() {
    if(!this.selectionDone) {
      let arr = [];
      for (let i = 0; i < this.responseData.length; i++) {
        arr.push(this.calcService.setValue(this.responseData[i]['baseRangeName'] + '', this.responseData[i].value ? 1 : 0))
      }
      Promise.all(arr).then((obj)=>{
        this.continue.emit(1);
        // this.router.navigate([this.routeTo, this.currentEventData.next_Pin_ID]);
        this.textEngineService.nextRoute(this.currentEventData['next_Pin_ID'], false);

      }).catch((err)=>{
        console.log(err);
      })
    }else {
      // this.router.navigate([this.routeTo, this.currentEventData.next_Pin_ID]);
      this.textEngineService.nextRoute(this.currentEventData['next_Pin_ID'], true);
    }
  }
}
