import { Injectable } from '@angular/core';
import { CalcService, StorageService, Constants } from '@btsdigital/ngx-isomer-core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RouteGuardextendedService  implements CanActivate {

  constructor(public calcService: CalcService, public router: Router, public storageService: StorageService) {  }
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    if (this.calcService.isApiReady()) {
      if(!this.currentRoundMatchesUrlRound(state)) {
        this.router.navigateByUrl('/intro');
        return false;
      }
      if(state.url != '/locked' && state.url != '/summary') {
        this.storageService.setValue(Constants.RETURN_URL, state.url);
      }else if(state.url == '/summary'){
        this.storageService.setValue(Constants.RETURN_URL, '/intro');
      }
      return true;
    } else {
      if(state.url != '/locked' && state.url != '/summary') {
        this.storageService.setValue(Constants.RETURN_URL, state.url);
      }else if(state.url != '/summary'){
        this.storageService.setValue(Constants.RETURN_URL, '/intro');
      }
      this.router.navigateByUrl('/splash');
      return false;
    }
  }
  currentRoundMatchesUrlRound(state: RouterStateSnapshot ) {
    let url = state.url;
    let currentRound = this.calcService.getValue('tlInputRound');
    let urlRound = url.indexOf('_R') == -1 ? null : url[url.length-1];
    if(urlRound) {
      return currentRound == urlRound;
    }else {
      return true;
    }
  }
}
