import { Component, OnInit } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-your-coach',
  templateUrl: './your-coach.component.html',
  styleUrls: ['./your-coach.component.styl']
})
export class YourCoachComponent implements OnInit {
  currentRound: string;
  coachContent: string;
  progressCount: string;
  private contentUpdateSubscription: Subscription;
  constructor(
    private textEngineService: TextEngineService,
    private router: Router,
    protected calcService: CalcService,
  ) { }

  ngOnInit() {
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.coachContent = this.textEngineService.getText('coachcontent');
    this.getProgress();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.coachContent = this.textEngineService.getText('coachcontent');
      this.getProgress();
    });
  }

  nextButton() {
    let nextNode = this.textEngineService.getText('coach_next_node', 'Intro_R' + this.currentRound);
    if (nextNode) {
      if (this.calcService.getValue('tlInputIntro2Completed_R' + this.currentRound) != '1') {
        this.calcService.setValue('tlInputIntro2Completed_R' + this.currentRound, 1);
      }
      this.router.navigate(['event/' + nextNode])
    } else {
      if (this.calcService.getValue('tlInputIntro4Completed_R' + this.currentRound) != '1') {
        this.calcService.setValue('tlInputIntro4Completed_R' + this.currentRound, 1);
      }
      this.router.navigate(['roundone']);
    }
  }

  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }
  ngOnDestroy(): void {
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe()
    }
  }

}
