import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BootstrapService } from '../bootstrap.service';
import { StorageService, Constants, CommunicatorService, CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { LoggerService } from '@btsdigital/pulseutilities';
import { REFS } from 'src/app/utils/constants';

@Component({
  selector: 'ism-splash',
  templateUrl: './splash.component.html',
  styleUrls: ['./splash.component.styl']
})
export class SplashComponent implements OnInit {

  private modelProgressSubscription: Subscription;
  progress = 0;

  constructor(private bootstraper: BootstrapService, private router: Router,
    private logger: LoggerService, private storageService: StorageService,
    private calcService: CalcService,
    private communicator: CommunicatorService) { }

  ngOnInit() {
    // console.log('oninit called', this.bootstraper.appReady)
    if (!this.bootstraper.appReady) {
      this.modelProgressSubscription = this.communicator
        .getEmitter(Constants.MODEL_LOAD_PROGRESS)
        .subscribe((progress) => this.progress = progress);

      this.bootstraper
        .init()
        .then(() => {
          this.appReady();
        });
    }else {
      this.appReady();
    }
  }

  private appReady() {
    this.logger.log('Model loaded');
    if (this.checkUserStatus()) {
      return;
    }
    let returnUrl: string;
    this.storageService.getValue(Constants.RETURN_URL, true)
      .then(val => {
        console.log(val)
        returnUrl = ((val) ? val : '/intro') + '';
        setTimeout(() => {
          this.router.navigateByUrl(returnUrl);
        }, );
      })
      .catch(err => {
        this.router.navigateByUrl('');
      });
  }
  checkUserStatus() {
    let round = localStorage.getItem("roundNo")
    if(round != null && round != 'null' && round){
      this.calcService.setValue(REFS.CURRENT_ROUND,round).then((obj:any)=>{
      }).catch((err)=>{
        console.log(err);
      })
    }
    let currentRound:any = this.calcService.getValue('tlInputRound');
    let roundCompletion:any = this.calcService.getValue('tlInputSubmitted_R'+currentRound)
    if(roundCompletion == 1) {
      this.naigateTo('summary');
      return true;
    }
    // else if(currentRound) {
    //   if( currentRound != 1) {
    //     let currentRoundInNumber = Number(currentRound);
    //     let previousRound =  currentRoundInNumber-1;
    //     let previousRoundCompletion:any = this.calcService.getValue('tlInputSubmitted_R'+previousRound);
    //     if(previousRoundCompletion != 1) {
    //       this.naigateTo('locked');
    //       return true;
    //     }
    //   } 
    // }
    return false
  }


  naigateTo(opt) {
    this.router.navigate([opt])
  }

}
