import { Component, OnInit, Input } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import * as Highcharts from 'highcharts';
import { HttpWrapperService, SyncService, CalcService, ManifestService } from '@btsdigital/ngx-isomer-core';
import { environment } from '../../../environments/environment';
import * as _ from 'lodash';
import { forIn } from 'lodash';
import { Subscription } from 'rxjs';
import { ChartData } from 'src/app/utils/chart-data';

@Component({
  selector: 'app-feedback-charts',
  templateUrl: './feedback-charts.component.html',
  styleUrls: ['./feedback-charts.component.styl']
})
export class FeedbackChartsComponent implements OnInit {
  @Input()
  sceneId;
  @Input()
  chartTitle;
  environment = environment;
  feedback: any;
  rangeref: any;
  year: any;
  serieslabels: any;
  categorylabels: any;
  categorylabelsObj: any = {};
  numberformat: any;
  chartoptions: any;
  chartOptionsData: any;
  participants: any;
  totalParticipants: any;
  actulData = [];
  partInfo: any;
  showChart = false;
  chartType: any;
  currentUserObj = [];
  minimumValue = null;
  maximumValue = null;
  state: any;
  isSlider:any;
  participantsStats = null;
  private contentUpdateSubscription: Subscription;
  constructor(
    public textEngineService: TextEngineService,
    public httpWrapperService: HttpWrapperService,
    public syncService: SyncService,
    public calcService: CalcService,
    private manifestService: ManifestService,

  ) { }

  ngOnInit() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.initializeAll();
    });
    
  }
  initializeAll() {
    this.getChartdata();
    if (this.environment.connectToPulse) {
      this.partInfo = this.syncService.getParticipantInfo();
      if (this.partInfo.participantId == undefined) {
        this.keepGettingPartInfo();
      } else {
        this.getLeaderboardData();
      }
    } else{
      this.getFakeChartData()
    }
  }

  getFakeChartData(){
    // this.actulData = ChartData[this.sceneId]
    // this.showChart = true;
    // this.createChart();
    let res = ChartData[this.sceneId]
    if(this.chartType == 'pie'){
      if(res.success) {
        this.participantsStats = JSON.parse(res.questions);
        delete this.participantsStats[this.rangeref[0]]['00'];
        this.showChart = true;
        this.processData();
      }
    }else if(this.chartType == 'bar'){
      if(res.success) {
        this.participantsStats = JSON.parse(res.questions);
        if(this.isSlider) {
          delete this.participantsStats[this.rangeref[0]]['00'];
          /**
           * We are subtracting user's vote, as user has not taken that decision yet
           * Default vote for slider is 1, so we are reducing the count of '01'(slider nameRange 'tlInputMoment3_R4') with number of users who had not taken the decision for slider yet, but started to work on sim.  
           */
          this.participantsStats[this.rangeref[0]]['01'] = this.participantsStats[this.rangeref[0]]['01'] - this.participantsStats[this.rangeref[1]]['00'];
        }
        this.showChart = true;
        this.processData();
      }
    }
  }

  getLeaderboardData() {

    if(this.chartType == 'pie') {
      let body = {
        questionNames: [...this.rangeref]
      }
      this.httpWrapperService.postJson('/Wizer/CloudFront/GetOptionCounts', body).then((res:any)=>{
        if(res.success) {
          this.participantsStats = JSON.parse(res.questions);
          delete this.participantsStats[this.rangeref[0]]['00'];
          this.showChart = true;
          this.processData();
        }
      }).catch((err)=>{
        console.error(err)
      })
    }else if (this.chartType == 'bar') {
      let body = {
        questionNames: [...this.rangeref]
      }
      if(!this.isSlider) {
        body['filterOptions'] = ['01']
      }
      this.httpWrapperService.postJson('/Wizer/CloudFront/GetOptionCounts', body).then((res:any)=>{
        if(res.success) {
          this.participantsStats = JSON.parse(res.questions);
          if(this.isSlider) {
            delete this.participantsStats[this.rangeref[0]]['00'];
            /**
             * We are subtracting user's vote, as user has not taken that decision yet
             * Default vote for slider is 1, so we are reducing the count of '01'(slider nameRange 'tlInputMoment3_R4') with number of users who had not taken the decision for slider yet, but started to work on sim.  
             */
            this.participantsStats[this.rangeref[0]]['01'] = this.participantsStats[this.rangeref[0]]['01'] - this.participantsStats[this.rangeref[1]]['00'];
          }
          this.showChart = true;
          this.processData();
        }
      }).catch((err)=>{
        console.error(err)
      })
    }

  }
  checkIfDataIsCorrect() {
    let isDataStored = true;
    if (this.chartType == 'pie') {
      for (let i = 0; i < this.participants.length; i++) {
        if (this.partInfo.participantId == this.participants[i].ParticipantId) {
          if (this.participants[i][this.rangeref[0]] != this.calcService.getValue(this.rangeref[0])) {
            this.participants[i][this.rangeref[0]] = this.calcService.getValue(this.rangeref[0])
          }
          break
        } else {
          continue;
        }
      }
    } else if (this.chartType == 'bar') {
      for (let i = 0; i < this.participants.length; i++) {
        if (this.partInfo.participantId == this.participants[i].ParticipantId) {
          for (let j = 0; j < this.rangeref.length; j++) {
            if (this.participants[i][this.rangeref[j]] != this.calcService.getValue(this.rangeref[j])) {
              this.participants[i][this.rangeref[j]] = this.calcService.getValue(this.rangeref[j])
            }
          }
        } else {
          continue;
        }
      }
    } else if (this.chartType == 'line') {
      for (let i = 0; i < this.participants.length; i++) {
        if (this.partInfo.participantId == this.participants[i].ParticipantId) {
          if (this.participants[i][this.rangeref[0]] != this.calcService.getValue(this.rangeref[0], true)) {
            this.participants[i][this.rangeref[0]] = this.calcService.getValue(this.rangeref[0], true)
          }
          break
        } else {
          continue;
        }
      }
    }
    return isDataStored;
  }
  processData() {
    this.actulData = [];
    this.currentUserObj = [];
    if (this.chartType == "pie") {
      
      for (const key in this.participantsStats[this.rangeref[0]]) {
        let obj = {
              name: this.categorylabelsObj[key],
              y: this.participantsStats[this.rangeref[0]][key]
          }
            this.actulData.push(obj)
      }
      for (let i = 0; i < this.categorylabels.length; i++) {
        let obj = {
          label: this.categorylabels[i],
          value: this.calcService.getValue(this.rangeref[0]) == (i + 1)+'' ? 1 : 0
        }
        this.currentUserObj.push(obj);
      }
      // console.log(this.currentUserObj)
    } else if (this.chartType == "bar") {
      let dataArray = []
      if (!this.isSlider) {
        for (let i = 0; i < this.rangeref.length; i++) {
          dataArray.push(this.participantsStats[this.rangeref[i]]['01'])
        }
        for (let i = 0; i < this.categorylabels.length; i++) {
          let obj = {
            label: this.categorylabels[i],
            value: this.calcService.getValue(this.rangeref[i]) == '1' ? 1 : 0
          }
          this.currentUserObj.push(obj);
        }
      } else {
        let keysLength = Object.keys(this.participantsStats[this.rangeref[0]]).length;
        for (let i = 0; i < keysLength; i++) {
          if(i<10) {
            dataArray.push(this.participantsStats[this.rangeref[0]]['0'+(i+1)])
          }else {
            dataArray.push(this.participantsStats[this.rangeref[0]][(i+1)])
          }
        }

        for (let i = 0; i < this.categorylabels.length; i++) {
          let obj = {
            label: this.categorylabels[i],
            value: this.calcService.getValue(this.rangeref[0]) == (i + 1)+'' ? 1 : 0
          }
          this.currentUserObj.push(obj);
        }
      }

      // console.log(dataArray)
      let obj = {
        name: this.serieslabels[0],
        data: dataArray,
        colorByPoint: true,
        dataLabels: {
          enabled: true,
          color: '#fff',
          formatter: function () {
            let total = this.series.data.reduce((a, b) => { return a + b.y }, 0);
            return this.y ? Math.round(100 * this.y / total) + '%' : '0%';
          }
        }
      }
      this.actulData.push(obj)

    } 
    this.createChart();
  }


  keepGettingPartInfo() {
    this.partInfo = this.syncService.getParticipantInfo();
    if (this.partInfo.participantId == undefined) {
      setTimeout(() => {
        // console.log('call again')
        this.keepGettingPartInfo()
      }, 1000);
      return;
    }
    this.getLeaderboardData();
  }

  getChartdata() {
    this.feedback = this.textEngineService.getScene(this.sceneId);
    this.chartType = this.feedback['chart']
    if (this.feedback["chartranges"]) {
      this.isSlider =  this.feedback["isSlider"] == "true" ? true : false;
      this.feedback["chartranges"] = this.feedback["chartranges"].replace(/&quot;/g, '"');
      this.rangeref = this.feedback["chartranges"] ? JSON.parse(this.feedback["chartranges"]) : [];
      this.categorylabels = this.feedback["chartcategories"] ? this.feedback["chartcategories"].split(',') : [];
      if(this.chartType == 'pie') {
        for (let i = 0; i < this.categorylabels.length; i++) {
          if(i<10) {
            this.categorylabelsObj['0'+(i+1)] = this.categorylabels[i];
          }else {
            this.categorylabelsObj[(i+1)] = this.categorylabels[i];
          }
        }
      }
      this.serieslabels = this.feedback["chartseries"] ? this.feedback["chartseries"].split(',') : [];
      this.year = this.feedback["chartyear"];
      this.chartTitle = this.feedback["charttitle"];
      this.numberformat = this.feedback["chartnumberformat"];
      if (this.feedback["chartOptions"] && this.feedback["chartOptions"] !== "") {
        try {
          this.chartoptions = JSON.parse(this.feedback["chartOptions"].replace(/'/g, '"'));
        }
        catch (e) {
          this.chartoptions = {};
        }
      }
    }
  }

  createChart() {
    this.chartOptionsData = {
      chart: {
        type: this.chartType,
        height: '300px'
      },
      title: {
        text: "",
      },
      legend: {
        itemMarginBottom: 10
      }

    };
    if (this.chartType == 'pie') {
      this.chartOptionsData['series'] = [{
        data: this.actulData
      }];
      this.chartOptionsData['tooltip'] = {
        pointFormat: '{point.name}: <b>{point.percentage:.2f}%</b>',
        headerFormat: '',
      }
      this.chartOptionsData['plotOptions'] = {
        pie: {
          allowPointSelect: true,
          cursor: 'pointer',
          dataLabels: {
            enabled: false
          },
          showInLegend: true
        }
      }


    } else if (this.chartType == 'bar') {
      this.chartOptionsData['xAxis'] = {}
      this.chartOptionsData['xAxis']['categories'] = this.categorylabels;
      this.chartOptionsData['yAxis'] = {
        title: {
          text: ''
        },
        labels: {
          formatter: function () {
            var total = this.chart.series[0].yData.reduce((a, b) => { return a + b }, 0)
            var percent = this.value * 100 / total;
            return percent.toFixed(0) + '%';
          }
        }
      }

      this.chartOptionsData['tooltip'] = {
        pointFormatter: function () {
          let total = this.series.data.reduce((a, b) => { return a + b.y }, 0)
          return this.series.name + ' : ' + Math.round(100 * this.y / total) + '%';
          // return this.series.name + ' : ' +  this.y;
        }
      }
      this.chartOptionsData['legend'] = {
        enabled: false
      }
      this.chartOptionsData['series'] = this.actulData;
      this.chartOptionsData['plotOptions'] = {}
      // this.chartOptionsData['plotOptions']['series'] = {
      //   stacking: 'percent'
      // }
      // this.chartOptionsData['chart']['height'] = "200px"
    } else if (this.chartType == 'line') {
      this.chartOptionsData['yAxis'] = {
        title: {
          text: 'million tonnes'
        },
        min: this.minimumValue,
        // max: this.maximumValue
      }
      this.chartOptionsData['xAxis'] = {
        visible: false
      }
      this.chartOptionsData['series'] = this.actulData;
    }
    // console.log(this.chartOptionsData)
    // this.chartOptionsData['legend']['itemMarginBottom'] = 5;
    let opt = {}
    _.merge(opt, this.chartOptionsData, this.chartoptions)
    if (document.getElementById('container'))
      Highcharts.chart('container', opt);
  }
  ngOnDestroy() { 
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }

}
