import { Component, Input, OnInit, ViewEncapsulation, HostListener } from '@angular/core';
import { CalcService, ConnectThrottlerService, SyncStatusService, StorageService } from '@btsdigital/ngx-isomer-core';
import * as _ from 'lodash';
import { Router } from '@angular/router';

declare var chrome: any;
const KEY_NUM_0 = 48; // used to reset sim

@Component({
  selector: 'ism-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.styl'],
  encapsulation: ViewEncapsulation.None
})

export class DashboardComponent implements OnInit {
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_0) {
      this.resetSim();
    }
  }
  constructor(
    private calcservice: CalcService,
    private connectThrottler: ConnectThrottlerService, 
    private syncStatusService: SyncStatusService,
    private storageService: StorageService,
    public router: Router
  ) { }

  ngOnInit() {
    // Subscribe to ErrorEmitter for error Codes
    this.connectThrottler.ErrorEmitter.subscribe((err: any) => {
      let errorCode = err.errCode;
      // Error Codes:
      // 101 - Saving decimal values to Option type of question
      // 102 - Saving out of range values to Option type of question
    });
    // Subscribe to Status for Sync Status
    this.syncStatusService.Status.subscribe((status: any) => {
      if (status === 2 || status === 3) {
        // this.openErrorModal();
      }
    });
  }

  resetSim() {
    const confirmResponse = confirm('Do you wish to reset sim progress? You cannot undo this.');
    if (confirmResponse) {
      let ManifestZone = window.localStorage.getItem('ManifestZone');
      ManifestZone = (ManifestZone !== null || ManifestZone !== 'null') ? ManifestZone : null;
      this.router.navigateByUrl('intro');
      this.storageService.clearAll()
        .then(() => {
          if (ManifestZone) {
            window.localStorage.setItem('ManifestZone', ManifestZone);
          }
          if (typeof chrome !== 'undefined' && chrome.runtime && chrome.runtime.reload) {
            chrome.runtime.reload();
          } else {
            window.location.reload();
          }
        });
    }
  }
}
