import { Injectable } from '@angular/core';
// import { setInterval } from 'timers';
import { Subject, Observable } from 'rxjs';
import { StorageService } from '@btsdigital/ngx-isomer-core';
import { GENERAL } from './utils/constants';
@Injectable()
export class WobblerTimingService {
  private showWobbler = new Subject<any>();
  private timeInterval: any;
  private secondsCount: number = 0;
  constructor(private dataStore: StorageService) { }

  init() {
  }

  startTime(time) {
    this.secondsCount = 0;
    // for(let i=0; i<100; i++)
    // {
    //     window.clearInterval(i);
    // }
    this.timeInterval = setInterval(() => {
      this.secondsCount++;
      if (this.secondsCount == time) {
        clearInterval(this.timeInterval._id);
        this.dataStore.setValue(GENERAL.WOBBLER_TIMING, "0");
        this.showWobbler.next();
      }
    }, 1000)
  }

  SavestopTimer() {
    this.dataStore.getValue(GENERAL.WOBBLER_TIMING, true).then((res) => {
      let storedTime = res && res != '' ? parseInt(res.toString()) : 0
      this.dataStore.setValue(GENERAL.WOBBLER_TIMING, (storedTime + this.secondsCount).toString());
    });
    if (this.timeInterval && this.timeInterval._id) {
      clearInterval(this.timeInterval._id);
    }
    this.dataStore.setValue('intervalId', JSON.stringify(this.timeInterval))
  }
  getObservable(): Observable<any> {
    return this.showWobbler.asObservable();
  }
  resetTimer() {
    window.clearInterval(this.timeInterval._id);
    console.log(this.timeInterval);
    this.dataStore.setValue(GENERAL.WOBBLER_TIMING, "0");
  }

}
