import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { AccordionConfig } from 'ngx-bootstrap/accordion';
import { TabsetComponent } from 'ngx-bootstrap/tabs';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs/internal/Subscription';

export function getAccordionConfig(): AccordionConfig {
  return Object.assign(new AccordionConfig(), { closeOthers: true });
}
@Component({
  selector: 'app-events',
  templateUrl: './events.component.html',
  styleUrls: ['./events.component.styl'],
  providers: [{ provide: AccordionConfig, useFactory: getAccordionConfig }]
})
export class EventsComponent implements OnInit {
  @ViewChild('staticTabs', { static: false }) staticTabs: TabsetComponent;
  currentEventData: any;
  sceneId = "Moment1_R1";
  responseData = [];
  selectedData: any;
  currentRound: any;
  currentScene: any;
  userSubscription: Subscription;
  private contentUpdateSubscription: Subscription;
  headerTitle: any;
  selectionDone = false;
  progressCount: string;
  minNoOfSelection:any;
  maxNoOfSelection:any;
  currentMoment:any;
  freeNumberValue;
  scenarioActualText;
  responseActualText;
  constructor(
    private textEngineService: TextEngineService,
    private calcService: CalcService,
    private route: ActivatedRoute,
    public router : Router,
    private cdRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.initializeAll();
    });
    
  }
  initializeAll() {
    this.currentMoment = this.textEngineService.getCurrentMoment();
    this.scenarioActualText = this.textEngineService.getText('scenario');
    this.responseActualText = this.textEngineService.getText('response');
    this.userSubscription = this.route.params.subscribe(routeParams => {

      this.currentEventData = null;
      this.cdRef.detectChanges();
      this.initializeData();
    });
  }

  public initializeData() {
    this.currentScene = this.route.snapshot.params.id;
    if (!this.currentScene) {
      this.currentScene = this.sceneId;
    }
    let sceneData = {};
    this.currentEventData = {};
    this.responseData = [];
    this.headerTitle = '';
    sceneData = this.textEngineService.getSceneById(this.currentScene);
    if (Object.keys(sceneData).length) {
      this.currentEventData = sceneData[this.currentScene];
      this.headerTitle = this.currentEventData.ID.replace(/_.*/, '');
      this.minNoOfSelection = this.currentEventData['min_selection'];
      this.maxNoOfSelection = this.currentEventData['max_selection'];
      let i = 1;
      let tempArray = [];
      for (let prop in this.currentEventData) {
        let objectData = {};
        objectData['option'] = this.currentEventData['alt' + i];
        objectData['name'] = this.currentEventData['alt' + i + '_name'];
        objectData['narrative'] = this.currentEventData['alt' + i + '_narrative'];
        objectData['value'] = false;
        let round = this.currentEventData.ID.split('_').pop();
        let moment = this.currentEventData.ID.split('_')[0];
        if(this.currentEventData.PageSubType == 'SingleSelect') {
          objectData['baseRangeName'] = 'tlInput'+this.currentEventData.ID;
        }else if(this.currentEventData.PageSubType == 'Drag_Drop') {
          objectData['baseRangeName'] = 'tlInput'+moment+'_Pos'+i+'_'+round;
        } else if (this.currentEventData.PageSubType == 'MultiSelect') {
          objectData['baseRangeName'] = 'tlInput'+moment+'_Selection'+i+'_'+round;
        }
        if(this.currentEventData['free_text'] == 'true') {
          objectData['rangeName'] = 'tlInput'+moment+'Text_'+round;
          objectData['freeText'] = true;
          objectData['freeTextLabel'] = this.currentEventData['free_text_label'];
          objectData['freeTextRange'] = this.currentEventData['free_text_range'] ? Number(this.currentEventData['free_text_range']) : this.currentEventData['free_text_range'];
        }else if(this.currentEventData['free_number'] == 'true'){
          objectData['rangeName'] = 'tlInput'+moment+'Text_'+round;
          objectData['freeNumber'] = true;
          objectData['freeNumberLabel'] = this.currentEventData['free_number_label'];
          objectData['freeNumberRange'] = this.currentEventData['free_number_range'] ? Number(this.currentEventData['free_number_range']) : this.currentEventData['free_number_range'];
        }
        tempArray.push(objectData);
        i++;
      }
      for (let i = 0; i < tempArray.length; i++) {
        if (tempArray[i].option) {
          this.responseData.push(tempArray[i]);
        }
      }
    }
    if(this.currentEventData.PageSubType == 'FreeText' || this.currentEventData.PageSubType == 'FreeNumber') {
      this.checkIfFreecheckIsDone();
    }
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.getProgress();
  }

  public selectTab(tabId: number) {
    this.staticTabs.tabs[tabId].active = true;
  }

  public onContinue(eventCompleted) {
    let round = this.currentEventData.ID.split('_').pop();
    let moment = this.currentEventData.ID.split('_')[0];
    this.calcService.setValue('tlInput'+moment+'DecCompleted_'+round, eventCompleted);
  }
  
  submitResponse() {
    let round = this.currentEventData.ID.split('_').pop();
    let moment = this.currentEventData.ID.split('_')[0];
    if(!this.selectionDone) {
      this.calcService.setValue('tlInput'+moment+'DecCompleted_'+round, 1).then((obj)=>{
        if(this.currentEventData.next_Pin_ID) {
          this.textEngineService.nextRoute(this.currentEventData['next_Pin_ID'], false);
        }else {
          this.router.navigate(['summary']);
        }
      }).catch((error)=>{
        console.log(error);
        
      });
    }else {
      if(this.currentEventData.next_Pin_ID) {
        this.textEngineService.nextRoute(this.currentEventData['next_Pin_ID'], true);
      }else {
        this.router.navigate(['summary']);
      }
    }
  }
  onActivate(event) {
    window.scroll(0, 0);
    //or document.body.scrollTop = 0;
    //or document.querySelector('body').scrollTo(0,0)
  }
  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }
  checkIfFreecheckIsDone() {
    let round = this.currentEventData.ID.split('_').pop();
      let moment = this.currentEventData.ID.split('_')[0];
      
      if (this.calcService.getValue('tlInput' + moment + 'DecCompleted_' + round) == '1') {
        this.selectionDone = true;
      }
  }

  ngOnDestroy(): void {
    if(this.userSubscription) {
      this.userSubscription.unsubscribe()
    }
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe()
    }
  }

}
