import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.styl']
})
export class ProfileComponent implements OnInit {
  currentRound: any;
  progressCount: string;

  constructor(
    private router: Router,
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.currentRound = this.calcService.getValue('tlInputRound');
    // this.welcomeContent = this.textEngineService.getText('welcomecontent')
    this.getProgress();
  }

  nextButton() {
   
  }

  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }
}
