import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';

@Component({
  selector: 'app-freenumber',
  templateUrl: './freenumber.component.html',
  styleUrls: ['./freenumber.component.styl']
})
export class FreenumberComponent implements OnInit {

  numberValue;
  @Output()
  onValueChange = new EventEmitter<any>();
  @Input() freeNumberLabel;

  @Input()
  rangeName;

  @Input()
  minlength = 0;//minimum length of string

  @Input()
  maxlength = 20000000;//maximum length of string

  @Input() id : any;

  placeholder = "Type here"
  selectionDone: boolean = false;
  responseData;
  private contentUpdateSubscription: Subscription;


  constructor(private calcService: CalcService,
    public textEngineService: TextEngineService,
    ) { }

  ngOnInit() {
    this.checkIfVisited();
    this.placeholder = this.textEngineService.getText('free_number_placeholder', this.id);
    // if(this.minlength && this.maxlength && this.maxlength !=20000000 ) {
    //   this.placeholder += ` in min ${this.minlength} and max ${this.maxlength} value.`
    // }else if(this.minlength) {
    //   this.placeholder += ` with min ${this.minlength} value.`
    // }else if (this.maxlength && this.maxlength !=20000000) {
    //   this.placeholder += ` with max ${this.maxlength} value.`
    // }
  }

  checkIfVisited() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.initializeAll();
    });
    
  }
  initializeAll() {
    setTimeout(() => {
      let round = this.id.split('_').pop();
      if(this.id) {
        this.responseData = this.textEngineService.getSceneById(this.id)[this.id]
      }
      let moment = this.id.split('_')[0];
      if (this.calcService.getValue('tlInput' + moment + 'DecCompleted_' + round) == '1') {
        this.numberValue = this.calcService.getValue(this.rangeName) ? this.calcService.getValue(this.rangeName).replace(/,/g, '') : null;
        this.selectionDone = true;
      }
    });
  }
  ngOnDestroy() { 
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }
  setValueToModel() {
    if(!this.selectionDone) {
      if(this.numberValue < this.minlength) {
        this.numberValue = this.minlength;
      }else if(this.numberValue > this.maxlength) {
        this.numberValue = this.maxlength;
      }
      this.calcService.setValue(this.rangeName, this.numberValue);
    }
  }

}
