import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';

@Component({
  selector: 'app-freetext',
  templateUrl: './freetext.component.html',
  styleUrls: ['./freetext.component.styl']
})
export class FreetextComponent implements OnInit {
  textValue;
  @Output()
  onTextChange = new EventEmitter<any>();
  @Input() freeTextLabel;

  @Input()
  rangeName;

  @Input()
  minlength;//minimum length of string

  @Input()
  maxlength = 20000000;//maximum length of string

  @Input() id : any;

  placeholder = "Type here"
  selectionDone: boolean = false;
  private contentUpdateSubscription: Subscription;

  constructor(private calcService: CalcService,
    public textEngineService: TextEngineService) { }

  ngOnInit() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.initializeAll();
    });
  }
  initializeAll() {
    this.checkIfVisited();
    this.placeholder = this.textEngineService.getText('free_text_placeholder', this.id);
    // if(this.minlength && this.maxlength && this.maxlength !=20000000 ) {
    //   this.placeholder += ` in min ${this.minlength} and max ${this.maxlength} characters.`
    // }else if(this.minlength) {
    //   this.placeholder += ` with min ${this.minlength} characters.`
    // }else if (this.maxlength && this.maxlength !=20000000) {
    //   this.placeholder += ` with max ${this.maxlength} characters.`
    // }
  }
  ngOnDestroy() { 
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }

  checkIfVisited() {
    setTimeout(() => {
      let round = this.id.split('_').pop();
      let moment = this.id.split('_')[0];
      let checkboxElement = document.getElementsByName("responseData");
      if (this.calcService.getValue('tlInput' + moment + 'DecCompleted_' + round) == '1') {
        this.textValue = this.calcService.getValue(this.rangeName);
        this.selectionDone = true;
      }
    });
  }
  setValueToModel() {
    if(!this.selectionDone && this.textValue && this.textValue.trim()) {
      this.calcService.setValue(this.rangeName, this.textValue);
    }
  }
}
