import { Component, OnInit, Input, ViewEncapsulation, ElementRef, ViewChild, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { CalcService, TextService, CommunicatorService, StorageService, Constants } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { CHANGE_LANGUAGE, LANGUAGES } from 'src/app/utils/constants';



@Component({
  selector: 'ism-intro',
  templateUrl: './intro.component.html',
  styleUrls: ['./intro.component.styl'],
})

export class IntroComponent implements OnInit, OnDestroy {

  // private currentYear: any;
  // @ViewChild('TeamNameLabel', { static: true }) private inputRef: ElementRef;
  // @ViewChild('TeamNameInputBox', { static: true }) private teamNameInpuRef: ElementRef;
  // @ViewChild('TeamYear', { static: true }) TeamYearInput: any;
  // private logoPath: string = '';
  // private isError: boolean = false;
  languageCode = LANGUAGES;
  selecteLanguage = null;
  currentRound: string;
  nickName:string;
  teamNamePlaceHolder:string;
  organisations = null;
  selecteOrg = null;
  namePlaceholder: string;
  introComplete: boolean = false;

  private contentUpdateSubscription: Subscription;
  constructor(protected calcService: CalcService,
    protected textService: TextService,
    private communicatorService: CommunicatorService,
    public textEngineService: TextEngineService,
    private router: Router,
    private cdref : ChangeDetectorRef,
    private storageService: StorageService) {
  }


  ngOnInit() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.initializeAll();
    });
    // this.logoPath = this.textService.getText('BtsLogo');
    // this.currentYear = this.calcService.getValue('xxRound');
    // //set value of the year
    // this.storageService.setValue(Constants.ROUND_VALUE, String(this.currentYear));
  }
  initializeAll() {
    // this.selecteLanguage = this.calcService.getValue('tlInputLanguage');
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.teamNamePlaceHolder = this.textEngineService.getText('Nickname');
    let org = this.textEngineService.getText('functions');
    this.organisations = org ? org.split(',') : null;
    this.namePlaceholder = this.textEngineService.getText('nameplaceholder');
    this.introComplete  = this.calcService.getValue('tlInputIntro1Completed_R1') == '1';
    this.selecteOrg = this.calcService.getValue('tlInputFunction_R1');
    if(this.selecteOrg && !this.introComplete && !this.organisations.includes(this.selecteOrg)) {
      this.selecteOrg = null;
    }
    if(this.introComplete){
      this.nickName = this.calcService.getValue('tlInputName_R1');
    }
    this.cdref.detectChanges();
  }
  setLanguage(ev) {
    if(ev) {
      this.communicatorService.trigger(CHANGE_LANGUAGE, ev);
    }
  }
  setOrgValue(ev) {
    this.calcService.setValue('tlInputFunction_R1', ev);
  }


  // onIntroSubmitClick() {
  //   if (this.teamNameInpuRef.nativeElement.value.length != 0) {
  //     this.calcService.setValue('tlInputTeamName', this.teamNameInpuRef.nativeElement.value, false, String(this.currentYear), 'Intro');
  //     this.communicatorService.trigger('APP_READY', true);
  //     this.storageService.setValue('INTRO_COMPLETE', 'true');
  //     // let url = ['overview', 'dashboard'];
  //     this.router.navigate(['event']);
  //   } else {
  //     this.isError = true;
  //   }
  // }

  // onTeamNameFocus() {
  //   this.inputRef.nativeElement.style.fontSize = "11px"
  //   this.inputRef.nativeElement.style.color = "#fff";
  //   this.inputRef.nativeElement.style.top = "-20px";
  //   this.inputRef.nativeElement.style.left = "0px";
  //   this.inputRef.nativeElement.style.transition = "top 1s, left 1s";
  // }
  // console.log('tlInputIntro1Completed_R' + this.currentRound, 1);

  nextButton() {
    if(this.nickName && this.nickName.trim()) {
      this.calcService.setValue('tlInputName_R1', this.nickName.trim());
      this.calcService.setValue('tlInputIntro1Completed_R' + this.currentRound, 1);
      this.router.navigate(['welcome']);
    }
  }

  ngOnDestroy() { 
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }
}
