import { Component, OnInit } from '@angular/core';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-roundone-intro',
  templateUrl: './roundone-intro.component.html',
  styleUrls: ['./roundone-intro.component.styl']
})
export class RoundoneIntroComponent implements OnInit {
  currentRound: string;
  introContent: string;
  progressCount: string;
  private contentUpdateSubscription: Subscription;

  constructor(
    private textEngineService: TextEngineService,
    private router: Router,
    protected calcService: CalcService,
  ) { }

  ngOnInit() {
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.currentRound = this.calcService.getValue('tlInputRound');
      this.introContent = this.textEngineService.getText('roundoneintrocontent');
      this.getProgress();
    });
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.introContent = this.textEngineService.getText('roundoneintrocontent');
    this.getProgress();
  }
  ngOnDestroy(): void {
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe()
    }
  }
  nextButton() {
    this.calcService.setValue('tlInputIntro5Completed_R' + this.currentRound, 1);
    this.router.navigate(['event']);
  }

  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }

}
