import { Component, OnInit, ViewChild, Input, Output, EventEmitter, ElementRef } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal/public_api';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';

@Component({
  selector: 'app-password',
  templateUrl: './password.component.html',
  styleUrls: ['./password.component.styl']
})
export class PasswordComponent implements OnInit {
  @ViewChild('PasswordModal', { static: true }) public passwordModalRef: ModalDirective;
  @Input() mainHeading = 'Password'
  @Input() messageKey = 'Enter the Password';
  @Input() passwordKey = '';
  @Input() placeholderText = 'Enter the Password';
  @Output() Authenticated: EventEmitter<any> = new EventEmitter();
  @ViewChild('PasswordModal', { static: true }) public passwordModal: ModalDirective;
  password = '';
  wrongPassword = false;
  modal: ElementRef;

  constructor(private textEngineService: TextEngineService) { }

  ngOnInit() {
  }

  showModal() {
    var body = document.getElementsByTagName("body")[0];
    this.modal = this.passwordModal["_element"];
    if (this.modal) {
      if(body.prepend){
        body.prepend(this.modal.nativeElement);
      }else{
        body.appendChild(this.modal.nativeElement)
      }
    }

    this.password = '';
    this.wrongPassword = false;
    this.passwordModalRef.show();
  }

  hideModal() {
    var body = document.getElementsByTagName("body")[0];
    if (this.modal) {
      body.removeChild(this.modal.nativeElement);
    }

    this.passwordModalRef.hide();
    this.password = '';
    this.wrongPassword = false;
  }

  onConfirmClicked() {
    const passwordValue = this.passwordKey;
    if (passwordValue == this.password) {
      console.log('password correct');
      this.Authenticated.next();
      this.hideModal();
    } else {
      this.wrongPassword = true;
      console.log('password incorrect');
    }
  }

}
