import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LogoutComponent } from './shared/logout/logout.component';
import { SplashComponent } from './shared/splash/splash.component';
import { IntroComponent } from './views/intro/intro.component';
import { CalcModelLoadedGuard } from '@btsdigital/ngx-isomer-core';
import { DashboardComponent } from './views/dashboard/dashboard.component';
import { EventsComponent } from './views/events/events.component';
import { FeedbackComponent } from './views/feedback/feedback.component';
import { CoachComponent } from './views/coach/coach.component';
import { WelcomescreenComponent } from './views/welcomescreen/welcomescreen.component';
import { ProfileComponent } from './views/profile/profile.component';
import { MissionComponent } from './views/mission/mission.component';
import { YourCoachComponent } from './views/your-coach/your-coach.component';
import { RoundoneIntroComponent } from './views/roundone-intro/roundone-intro.component';
import { SummaryComponent } from './views/summary/summary.component';
import { ProjectBriefComponent } from './views/project-brief/project-brief.component';
import { LockedComponent } from './views/locked/locked.component';
import { RouteGuardextendedService } from './utils/route-guard-extended.service';

const routes: Routes = [
  {
    path: 'splash',
    component: SplashComponent
  },
  {
    path: 'logout',
    component: LogoutComponent
  },
  {
    path: 'intro',
    component: IntroComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'locked',
    component: LockedComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'welcome',
    component: WelcomescreenComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'mission',
    component: MissionComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'profile',
    component: ProfileComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'yourcoach',
    component: YourCoachComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'roundone',
    component: RoundoneIntroComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'event',
    component: EventsComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'event/:id',
    component: EventsComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'feedback/:id',
    component: FeedbackComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'coach/:id',
    component: CoachComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'infoupdate/:id',
    component: ProjectBriefComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: 'summary',
    component: SummaryComponent,
    canActivate: [RouteGuardextendedService]
  },
  {
    path: '',
    redirectTo: '/splash',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
