import { Component, OnInit, Input, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { CHANGE_LANGUAGE, LANGUAGES } from 'src/app/utils/constants';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.styl']
})
export class HeaderComponent implements OnInit {
  @Input() titleText: any;
  headerShow: boolean;
  menuArray = [
  ];
  currentRound: any;
  score: string;
  currentMoment: any;
  languageCode = LANGUAGES;
  selecteLanguage;
  private contentUpdateSubscription: Subscription;
  constructor(
    private router: Router,
    public calcService: CalcService,
    private injector: Injector,
    private communicatorService: CommunicatorService,
    public textEngineService: TextEngineService
  ) { }

  ngOnInit() {
    this.getScore();
  }
  setLanguage(ev) {
    this.communicatorService.trigger(CHANGE_LANGUAGE, ev);
  }
  openNav() {
    this.currentMoment = this.textEngineService.getCurrentMoment();
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.selecteLanguage = this.calcService.getValue('tlInputLanguage');
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.initializeAll();
    });
    // for (let i = 0; i < this.menuArray.length; i++) {
    //   if (this.menuArray[i]['label'].includes('Scenario')) {
    //     let currentMoment = this.menuArray[i]['label'].split(' ').pop();
    //     if(currentMoment < 5) {
    //       if (this.calcService.getValue('tlInputMoment' + currentMoment + 'CoachCompleted_R' + this.currentRound) == '1') {
    //         this.menuArray[i]['isCompleted'] = true;
    //       }
    //     }else {
    //       if(this.calcService.getValue('tlInputMoment'+currentMoment+'FBCompleted_R'+this.currentRound) == '1') {
    //         this.menuArray[i]['isCompleted'] = true;
    //       }
    //     }
    //   } else if (this.menuArray[i]['label'].includes('Welcome')) {
    //     // let value;
    //     // if (this.currentRound != '1') {
    //     //   value = this.calcService.getValueForYear('tlInputIntro1Completed', 'tlInputRound');
    //     // } else {
    //     //   value = this.calcService.getValueForYear('tlInputIntro2Completed', 'tlInputRound');
    //     // }
    //     // if (value == '1') {
    //       //WELCOME SCREEN NEEDS BE ENABLED ALWAYS
    //       this.menuArray[i]['isCompleted'] = true;
    //     // }
    //   } else if (this.menuArray[i]['label'].includes('Leader')) {
    //     let val = this.calcService.getValueForYear('tlInputSubmitted', 'tlInputRound');
    //     if (val == '1') {
    //       this.menuArray[i]['isCompleted'] = true;
    //     }
    //   }
    // }
  }
  initializeAll() {
    this.menuArray = [{
      label: this.textEngineService.getText('welcomescreen'),
      isCompleted: true,
      isHidden: false
    },
    {
      label: this.textEngineService.getText('scenario1'),
      isCompleted: this.isEqual('tlInputMoment1CoachCompleted', '1', 'tlInputRound'),
      isHidden: false
    },
    {
      label: this.textEngineService.getText('scenario2'),
      isCompleted: this.isEqual('tlInputMoment2CoachCompleted', '1', 'tlInputRound'),
      isHidden: false
    },
    {
      label: this.textEngineService.getText('scenario3'),
      isCompleted: this.isEqual('tlInputMoment3CoachCompleted', '1', 'tlInputRound'),
      isHidden: false
    },
    {
      label: this.textEngineService.getText('scenario4'),
      isCompleted: this.isEqual('tlInputMoment4CoachCompleted', '1', 'tlInputRound'),
      isHidden: false
    },
    {
      label: this.textEngineService.getText('scenario5'),
      isCompleted: this.isEqual('tlInputMoment5FBCompleted', '1', 'tlInputRound'),
      isHidden: !this.isEqual('tlInputRound', '1')
    },
    {
      label: this.textEngineService.getText('leaderboard'),
      isCompleted: this.isEqual('tlInputSubmitted', '1', 'tlInputRound'),
      isHidden: false
    },
    {
      label: this.textEngineService.getText('signout'),
      isCompleted: true,
      isHidden: true
    }
  ]
    document.getElementById("mySidenav").style.width = "100%";
  }
  getText() {
    this.currentMoment = this.textEngineService.getCurrentMoment();

    if (this.router.url.includes('/summary')) {
      return this.textEngineService.getText('summary')+' '
    } else if (this.router.url.includes('/event')) {
      return this.textEngineService.getText('moment')+' ' + this.currentMoment
    } else if (this.router.url.includes('/coach')) {
      return this.textEngineService.getText('coach')+' ' + this.currentMoment
    } else if (this.router.url.includes('/feedback')) {
      return this.textEngineService.getText('feedback')+' ' + this.currentMoment
    } else {
      return '';
    }
  }
  ngOnDestroy() { 
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }

  closeNav() {
    document.getElementById("mySidenav").style.width = "0";
  }

  getScore() {
    this.score = this.calcService.getValueForYear('tlOutputMetric1', 'tlInputRound');
  }

  routeTo(menu) {
    switch (menu) {
      case this.textEngineService.getText('welcomescreen'):
        this.closeNav();
        this.router.navigateByUrl('welcome');
        break;
      case this.textEngineService.getText('scenario1'):
        this.closeNav();
        // this.router.navigate(['event', this.coachData['next_Pin_ID']]);
        this.router.navigateByUrl('event/Moment1_R' + this.currentRound);
        break;
      case this.textEngineService.getText('scenario2'):
        this.closeNav();

        this.router.navigateByUrl('event/Moment2_R' + this.currentRound);
        break;
      case this.textEngineService.getText('scenario3'):
        this.closeNav();

        this.router.navigateByUrl('event/Moment3_R' + this.currentRound);
        break;
      case this.textEngineService.getText('scenario4'):
        this.closeNav();
        this.router.navigateByUrl('event/Moment4_R' + this.currentRound);
        break;
      case this.textEngineService.getText('scenario5'):
        this.closeNav();
        this.router.navigateByUrl('event/Moment5_R' + this.currentRound);
        break;
      case this.textEngineService.getText('leaderboard'):
        this.closeNav();
        this.router.navigateByUrl('summary');
        break;
      case this.textEngineService.getText('signout'):
        this.closeNav();
        this.router.navigateByUrl('logout');
        break;
      default:
        this.closeNav();
        break;
    }
  }

  isEqual(rangename, expectedvalue, yearRef = ''){
    let actualvalue = this.calcService.getValueForYear(rangename, yearRef)
    return actualvalue == expectedvalue;
  }
}
