import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-coach',
  templateUrl: './coach.component.html',
  styleUrls: ['./coach.component.styl']
})
export class CoachComponent implements OnInit, OnDestroy {
  currentScene: any;
  coachData: any;
  coachtitletext = 'In the Tetra Pak context';
  progressCount: string;
  currentRound: string;
  currentMoment: any;
  currentLanguage: any;
  contentUpdateSubscription: Subscription;

  constructor(
    private route: ActivatedRoute,
    private textEngineService: TextEngineService,
    private router: Router,
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.currentMoment = this.textEngineService.getCurrentMoment();
    this.initializeCoachComment();
    this.markAsComplete()
    this.currentLanguage = this.calcService.getValue('tlInputLanguage')
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
      this.currentLanguage = this.calcService.getValue('tlInputLanguage')
      this.initializeCoachComment();
    });
  }

  ngOnDestroy() {
    if (this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe()
    }
  }

  public initializeCoachComment() {
    this.currentScene = this.route.snapshot.params.id;
    let sceneData = this.textEngineService.getSceneById(this.currentScene);
    this.coachData = sceneData[this.currentScene];
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.getProgress();
  }

  public nextButton() {
    let rangeName = this.currentScene.split("_");
    if (this.calcService.getValue('tlInput' + rangeName[0] + 'Completed_' + rangeName[1] + '') != '1') {
      this.calcService.setValue('tlInput' + rangeName[0] + 'Completed_' + rangeName[1] + '', 1).then(() => {
        if (this.coachData['next_Pin_ID']) {
          this.textEngineService.nextRoute(this.coachData['next_Pin_ID']);
        } else {
          this.calcService.setValue('tlInputEORFbCompleted_R' + this.currentRound, 1).then((response: any) => {
            this.router.navigate(['summary']);
          });
        }
      });
    }else {
      if(this.coachData['next_Pin_ID']) {
        let obj:any = this.textEngineService.getSceneById(this.coachData['next_Pin_ID'])[this.coachData['next_Pin_ID']];
          this.textEngineService.nextRoute(this.coachData['next_Pin_ID']);
      }else {
        if(this.calcService.getValue('tlInputEORFbCompleted_R'+this.currentRound) != '1'){
          this.calcService.setValue('tlInputEORFbCompleted_R'+this.currentRound, 1).then((response:any)=>{
            this.router.navigate(['summary']);
          });
        }else{
          this.router.navigate(['summary']);
        }
      }

    }
  }

  getProgress() {
    this.progressCount = this.calcService.getValue('tlOutputProgress_R' + this.currentRound);
  }

  markAsComplete(){
    let rangeName = this.currentScene.split("_");
    if (this.calcService.getValue('tlInput' + rangeName[0] + 'Completed_' + rangeName[1] + '') != '1') {
      this.calcService.setValue('tlInput' + rangeName[0] + 'Completed_' + rangeName[1] + '', 1).then(() => {
        if (this.coachData['next_Pin_ID'] == undefined) {
          this.calcService.setValue('tlInputEORFbCompleted_R' + this.currentRound, 1).then(()=>{
            // console.log('Round Completed')
          })
        }
      });
    }
  }

}
