import { AfterViewInit, Component, Input, OnInit, ViewChild, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import * as noUiSlider from 'nouislider';
import { CalcService, IsomerXapiService, NumberFormatting, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextEngineService } from 'src/app/shared/textengine/textengine.service';
import { Router } from '@angular/router';

/**
 * Calc Slider component, allows the user to update inputs using a slider instead of a standard text input box
 *
 */
@Component({
  selector: 'isma-calc-slider',
  templateUrl: './calcslider.html',
  styleUrls: ['./calcslider.styl']
})
export class CalcSlider implements OnInit, AfterViewInit {
  private subscription: Subscription;
  isloaded = false
  initially = true;
  isFirstTime = true;
  @Input()
  sceneId
  /**
   * Input binding for range ref
   *
   */
  ref: string;
  /**
   * Input binding for year ref
   *
   */
  yearRef: string;
  /**
   * Input binding for Step value for input element
   */
  step: number;
  /**
   * Input binding for Min value for input element
   *
   */
  min: number = 0;
  /**
   * Input binding for Max value for input element
   *
   */
  max: number = 9999999999999999;
  /**
   * Input binding flag to show / hide slider buttons
   *
   */
  showButtons: boolean = false;
  /**
   * Input binding flag to show / hide slider buttons
   *
   */
  format: string;
  /**
   * Input binding flag to show / hide slider buttons
   *
   */
  scaler: number = 1;
  /**
* Input binding for xapiTrackingOnPulse
*/
  xapiTrackingOnPulse: boolean = false;
  /**
 * Input binding for xapiTrackingOnLoad
 */
  xapiTrackingOnLoad: boolean = null;

  @Output() sliderOutput: EventEmitter<any> = new EventEmitter();
  @Output() continue: EventEmitter<any> = new EventEmitter<any>();
  /**
   * Value for range ref from calc-model
   */
  public value: string | number = 1;
  /**
   * Updated value on the input field that is not yet synced to model
   *
   */
  private tempValue: string;
  /**
   * Year value from model
   *
   */
  private year: string;
  /**
   * Flag value to check whether slider value was changed from initial value
   *
   */
  private isDirty: boolean = false;
  /**
   * Html element where the slider needs to be rendered
   *
   */
  @ViewChild('calcSliderEl', { static: false }) elRef: any;
  currentMoment: any;
  currentRound: string;
  sliderData: any;
  selectionDone = false;
  private contentUpdateSubscription: Subscription;
  /**
   * Constructor for calc-slider component
   *
   * @param {CalcService} calcService CalcService instance
   *
   */
  constructor(
    private calcService: CalcService,
    private isomerXapiService: IsomerXapiService,
    private communicator: CommunicatorService,
    private cdRef: ChangeDetectorRef,
    public textEngineService : TextEngineService,
    public router : Router
  ) { }

  /**
   * OnInit lifecycle function to instantiate component
   *
   */
  ngOnInit() {
    this.initializeAll();
    this.contentUpdateSubscription = this.textEngineService.getObservable().subscribe(() => {
        this.initializeAll();
    });
  }
  initializeAll() {
    this.currentRound = this.calcService.getValue('tlInputRound');
    this.currentMoment = this.textEngineService.getCurrentMoment();
    this.ref = 'tlInputMoment'+this.currentMoment;
    this.yearRef = 'tlInputRound';
    this.getAllData();
    this.checkIfVisited();

  }
  ngOnDestroy() { 
    if(this.contentUpdateSubscription) {
      this.contentUpdateSubscription.unsubscribe();
    }
  }
  checkIfVisited() {
    setTimeout(() => {
      if (this.calcService.getValue('tlInputMoment' + this.currentMoment + 'DecCompleted_R' + this.currentRound) == '1') {
        this.selectionDone = true;
        this.initially = false;
        const slider = this.elRef.nativeElement;
        let value:any = this.calcService.getValueForYear(this.ref, this.yearRef);
        value = NumberFormatting.unformat(value);
        slider.noUiSlider.set(value, false);
        slider.setAttribute('disabled', true);

      }
    });
  }
  getAllData() {
    this.sliderData = this.textEngineService.getScene(this.sceneId);
    if(this.sliderData) {
      this.step = this.sliderData['step'];
      this.min = 1;
      this.max = Number(this.sliderData['total_alt']);
      setTimeout(() => {
      this.value = this.calcService.getValueForYear(this.ref, this.yearRef);
        this.value = NumberFormatting.unformat(this.value);
        // console.log(this.value)
        this.cdRef.detectChanges()
      }, 500);
      // this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
      //   this.update();
      //   this.cdRef.markForCheck();
      // });
    }
  }
  getText(){
    return this.sliderData['alt'+this.value+'_narrative']
    setTimeout(() => {
    }, );
  }
  update() {
    let value:any = this.calcService.getValueForYear(this.ref, this.yearRef);
    value = NumberFormatting.unformat(value);
    if(value != this.value){
      this.value = value
      const slider = this.elRef.nativeElement;
      slider.noUiSlider.set(value, false);
    }
  }

  ngAfterViewInit() {
    const slider = this.elRef.nativeElement,
      opt: noUiSlider.Options = {
        start: [Number(this.value) || Number(this.min)],
        connect: true,
        range: {
          'min': Number(this.min),
          'max': Number(this.max)
        },
        step: (this.step) ? Number(this.step) : undefined,
        pips: {
          mode: 'steps',
          density: 1
        },
        behaviour: 'tap',
        format: {
          to: (value) => {
            return NumberFormatting.format(value, this.format, this.scaler);
          },
          from: (value) => {
            return NumberFormatting.unformat(value, this.scaler);
          }
        }
      };
    // track data on load
    this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.ref),
      this.calcService.getRefNameByYearRef(this.ref, this.yearRef), String(this.value),
      this.yearRef ? this.calcService.getValue(this.yearRef) : this.calcService.getRoundValue(), 'calc-slider', this.xapiTrackingOnLoad, this.xapiTrackingOnPulse);

    noUiSlider.create(slider, opt);
    this.bindEvents(slider);
    setTimeout(() => {
      this.isloaded = true;
    }, );
  }

  /**
   * Function to bind events to slider
   *
   * @param {any} slider Slider element reference
   */
  bindEvents(slider: any) {
    if (slider && slider.noUiSlider) {
      slider.noUiSlider.on('update', () => {
        let curVal = slider.noUiSlider.get();
        if (this.scaler != 1) {
          curVal = curVal / this.scaler;
        }
        this.value = curVal;
        if(!this.isFirstTime) {
          this.initially = false;
        }
        this.isFirstTime = false;
        this.saveDataToModel(curVal);
      });
    }
  }

  /**
   * Function to save component value back to model
   *
   * @param {any} value Value to be saved to model
   */
  saveDataToModel(val: any) {
   
  }
  public submitResponse() {
    if(!this.selectionDone) {
      this.calcService.setValueForYear(this.ref, this.value, this.yearRef, 'calc-slider', this.xapiTrackingOnPulse).then((obj)=>{
        this.continue.emit(1);
        // this.router.navigate(['feedback', this.sliderData.next_Pin_ID]);
        this.textEngineService.nextRoute(this.sliderData['next_Pin_ID'], false);

      }).catch((err)=>{
        console.log(err)
      })
    }else {
      // this.router.navigate(['feedback', this.sliderData.next_Pin_ID]);
      this.textEngineService.nextRoute(this.sliderData['next_Pin_ID'], true);
    }
  }
}
